// ÂéªÊéâÂ§ö‰ΩôÁöÑ0
export function subZeroAndDot(price) {
  // console.log(price)
  if (!price) return "0";
  if (typeof price == "undefined" || price == null || price.trim() == "") {
    return price;
  }
  return Number(String(parseFloat(price)));
}
