function downFixed(num, fix) {
  // num‰∏∫ÂéüÊï∞Â≠óÔºåfixÊòØ‰øùÁïôÁöÑÂ∞èÊï∞‰ΩçÊï∞
  let result = "0";
  if (Number(num) && fix > 0) {
    // ÁÆÄÂçïÁöÑÂÅö‰∏™Âà§Êñ≠
    fix = +fix || 2;
    num = num + "";

    if (/e/.test(num)) {
      // Â¶ÇÊûúÊòØÂåÖÂê´eÂ≠óÁ¨¶ÁöÑÊï∞Â≠óÁõ¥Êé•ËøîÂõû
      // result = num
      result = 0;
    } else if (!/\./.test(num)) {
      // Â¶ÇÊûúÊ≤°ÊúâÂ∞èÊï∞ÁÇπ
      result = num + `.${Array(fix + 1).join("0")}`;
    } else {
      // Â¶ÇÊûúÊúâÂ∞èÊï∞ÁÇπ
      num = num + `${Array(fix + 1).join("0")}`;
      let reg = new RegExp(`-?\\d*.\\d{0,${fix}}`);
      result = reg.exec(num)[0];
    }
  }
  return result;
}
export function toChinesNum(num) {
  let changeNum = ["Èõ∂", "‰∏Ä", "‰∫å", "‰∏â", "Âõõ", "‰∫î", "ÂÖ≠", "‰∏É", "ÂÖ´", "‰πù"];
  let unit = ["", "ÂçÅ", "Áôæ", "ÂçÉ", "‰∏á"];
  num = parseInt(num);
  let getWan = (temp) => {
    let strArr = temp.toString().split("").reverse();
    let newNum = "";
    for (var i = 0; i < strArr.length; i++) {
      newNum =
        (i == 0 && strArr[i] == 0
          ? ""
          : i > 0 && strArr[i] == 0 && strArr[i - 1] == 0
          ? ""
          : changeNum[strArr[i]] + (strArr[i] == 0 ? unit[0] : unit[i])) +
        newNum;
    }
    return newNum;
  };
  let overWan = Math.floor(num / 10000);
  let noWan = num % 10000;
  if (noWan.toString().length < 4) {
    noWan = "0" + noWan;
  }
  return overWan ? getWan(overWan) + "‰∏á" + getWan(noWan) : getWan(num);
}
// Âà§Êñ≠ÊòØÂá†ÁßíÂâç
export function getDateDiff(setdateTimeStamp) {
  let minute = 1000 * 60;
  let hour = minute * 60;
  let day = hour * 24;
  let halfamonth = day * 15;
  let month = day * 30;
  let dateTimeStamp = new Date(setdateTimeStamp * 1000);
  let now = new Date().getTime();
  let diffValue = now - dateTimeStamp;
  if (diffValue < 0) {
    //Ëã•Êó•Êúü‰∏çÁ¨¶ÂàôÂºπÂá∫Á™óÂè£Âëä‰πã
    return "ÁªìÊùüÊó•Êúü‰∏çËÉΩÂ∞è‰∫éÂºÄÂßãÊó•ÊúüÔºÅ";
  }
  let monthC = diffValue / month;
  let weekC = diffValue / (7 * day);
  let dayC = diffValue / day;
  let hourC = diffValue / hour;
  let minC = diffValue / minute;
  let result = "";
  if (monthC >= 1) {
    // result = parseInt(monthC) + locale.BROWSER.GYQ;
    result = parseInt(monthC) + "Êúà";
  } else if (weekC >= 1) {
    result = parseInt(weekC) + "Âë®";
  } else if (dayC >= 1) {
    result = parseInt(dayC) + "Â§©";
  } else if (hourC >= 1) {
    result = parseInt(hourC) + "Â∞èÊó∂";
  } else if (minC >= 1) {
    result = parseInt(minC) + "ÂàÜÈíü";
    // console.log( parseInt(diffValue/1000))
  } else result = parseInt(diffValue / 1000) + locale.BROWSER.MQ;

  // result = parseInt(diffValue);
  // alert(result);
  return result;
}
