//Áî±ÊòüÊúüÁöÑÊï∞Â≠óËΩ¨Êç¢‰∏∫ÊòüÊúüÁöÑÂêçÁß∞,dateÊ†ºÂºèÔºö2016-01-07ÔºåÁî®"-"ÂàÜÂâ≤
export function toWeekName(date) {
  var newDate = new Date(date.replace(/-/g, "/"));
  var weekday = new Array(7);
  weekday[0] = "ÊòüÊúüÊó•";
  weekday[1] = "ÊòüÊúü‰∏Ä";
  weekday[2] = "ÊòüÊúü‰∫å";
  weekday[3] = "ÊòüÊúü‰∏â";
  weekday[4] = "ÊòüÊúüÂõõ";
  weekday[5] = "ÊòüÊúü‰∫î";
  weekday[6] = "ÊòüÊúüÂÖ≠";
  return weekday[newDate.getDay()];
}
//Ëß£ÂÜ≥iosÊ©°ÁöÆÁ≠ãÈóÆÈ¢ò
export function iosTrouchFn(el) {
  //elÈúÄË¶ÅÊªëÂä®ÁöÑÂÖÉÁ¥†
  el.addEventListener("touchmove", function (e) {
    e.isSCROLL = true;
  });
  document.body.addEventListener(
    "touchmove",
    function (e) {
      if (!e.isSCROLL) {
        e.preventDefault(); //ÈòªÊ≠¢ÈªòËÆ§‰∫ã‰ª∂(‰∏ä‰∏ãÊªëÂä®)
      } else {
        //ÈúÄË¶ÅÊªëÂä®ÁöÑÂå∫Âüü
        var top = el.scrollTop; //ÂØπË±°ÊúÄÈ°∂Á´ØÂíåÁ™óÂè£ÊúÄÈ°∂Á´Ø‰πãÈó¥ÁöÑË∑ùÁ¶ª
        var scrollH = el.scrollHeight; //Âê´ÊªöÂä®ÂÜÖÂÆπÁöÑÂÖÉÁ¥†Â§ßÂ∞è
        var offsetH = el.offsetHeight; //ÁΩëÈ°µÂèØËßÅÂå∫ÂüüÈ´ò
        var cScroll = top + offsetH; //ÂΩìÂâçÊªöÂä®ÁöÑË∑ùÁ¶ª

        //Ë¢´ÊªëÂä®Âà∞ÊúÄ‰∏äÊñπÂíåÊúÄ‰∏ãÊñπÁöÑÊó∂ÂÄô
        if (top == 0) {
          top = 1; //0ÔΩû1‰πãÈó¥ÁöÑÂ∞èÊï∞‰ºöË¢´ÂΩìÊàê0
        } else if (cScroll === scrollH) {
          el.scrollTop = top - 0.1;
        }
      }
    },
    { passive: false }
  ); //passiveÈò≤Ê≠¢ÈòªÊ≠¢ÈªòËÆ§‰∫ã‰ª∂‰∏çÁîüÊïà
}
export function formatZeroNumber(price) {
  if (!price) return 0;
  if (price>0){
    return formatWalletPrice(price)
  }
  const nStr = price.toString();
  // ÁÇπÂºÄÂ§¥ ÂêéÈù¢Êé•‰ªªÊÑè‰∏™0 ‰πüÂèØËÉΩÊ≤°Êúâ0 ÁÑ∂ÂêéÊòØÈùû0ÁöÑÊï∞Á¨¨‰∏ÄÊ¨°Âá∫Áé∞ ÂêéÈù¢Êé•‰ªªÊÑèÊï∞Â≠ó
  const pattern = /\.0*?[^0].?/;
  return nStr.split(".")[0] + pattern.exec(nStr)[0];
}
